.team-modal {
  position: relative;

  margin: 40px auto;
  width: 280px;
  padding: 110px 20px 100px;
  border-radius: 150px 0px;

  background: var(--modal-bg-color);

  @include mq(tablet) {
    border-radius: 175px 0px;
    padding: 72px;
    width: 704px;
  }
  @include mq(desktop) {
    width: 1035px;
  }
}
.modal-button {
  position: absolute;
  width: 18px;
  height: 18px;
  right: 25px;
  top: 25px;
  border: none;

  &::before,
  &::after {
    position: absolute;
    top: -4;
    left: 10px;
    content: '';
    height: 32px;
    width: 3px;
    background-color: var(--btn-bg-color);
  }

  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
  }
  &:hover::before,
  &:hover::after {
    background-color: var(--accent-color);
  }

  @include mq(tablet) {
    width: 25px;
    height: 25px;
    right: 32px;
    top: 32px;
  }
}

.team__title {
  font-weight: 900;

  text-align: center;
  text-transform: uppercase;
  margin-bottom: 60px;

  color: var(--accent-color);
  @include mq(mobile) {
    font-size: 28px;
    line-height: 34px;
  }
}
.team__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px 12px;
  @include mq(tablet) {
    gap: 40px 40px;
  }
  @include mq(desktop) {
    gap: 40px 12px;
  }
}

.team__card {
  display: block;
  position: relative;
  width: 239px;

  @include mq(tablet) {
    width: 260px;
  }
  @include mq(desktop) {
    width: 287px;
  }
}
.card__decoration {
  position: absolute;
  width: 184px;
  height: 172px;
  top: 0;
  right: 0;
  @include mq(tablet) {
    width: 205px;
    height: 192px;
  }
  @include mq(desktop) {
    width: 227px;
    height: 212px;
  }
}
.team__card-image {
  position: relative;
  z-index: 1;
  width: 220px;
  height: 220px;
  margin-top: 20px;
  margin-bottom: 10px;
  border-radius: 50px 0px;

  object-fit: cover;
  @include mq(tablet) {
    width: 240px;
    height: 240px;
  }
  @include mq(desktop) {
    width: 267px;
    height: 267px;
  }
}
.team__tumb {
  //   display: flexbox;
  align-items: center;
  justify-content: center;
}
.team__name-dev {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  margin-bottom: 5px;

  color: var(--accent-color);
}
.team__descr {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  margin-bottom: 15px;

  color: #4f4f4f;
}
.social__list {
  display: flex;
  justify-content: center;
  gap: 15px;
}
.social__item {
  display: block;
  width: 30px;
  height: 30px;
  &:hover {
    fill: var(--accent-color);
  }
}
